import './about.css'
import im from '../../media/mandy_and_angelo.jpeg'
import {motion, useDragControls} from 'framer-motion';
import { useState,useRef } from 'react';

function About() {

    const [toggled,setToggled] = useState(false)
    const [toggledRight,setToggledRight] = useState(false)
    const [toggledLeft,setToggledLeft] = useState(false)
    const [tmpTitle,setTmpTitle] = useState(true)
    const [mandyActive,setMandyActive] = useState(false)
    const [angeloActive,setAngeloActive] = useState(false)

    const knowbStyle = {
        width:15,
        height:15,
        borderRadius:100,
        backgroundColor:'black',
        opacity:0.9,
        borderWidth:0,
        boxShadow:'1px 1px 9px gray'
    }

    const controls = useDragControls()
    const e = (l,r) => {
        if (l){
            return(-170)
        }
        if (r){
            return(170)
        }
    }

    const mangeloSpan = <span>
            <span color='#15433D'>M</span>
            <span color='#15433D'>A</span>
            <span color='#15433D'>N</span>
            <span color='#EDA52F'>G</span>
            <span color='#EDA52F'>E</span>
            <span color='#EDA52F'>L</span>
            <span color='#EDA52F'>O</span>
        </span>

    const mandySpan = <span>{
            'MANDY'.split("").map((char,index)=>{
                const style = {"animation-delay": (0.1 + index / 10) + "s",color:'#15433D'};
                return <span
                    aria-hidden="true"
                    key={index}
                    style={style}>
                    {char}
                </span>;
            })
        }</span>
    
    const angeloSpan = <span>{
        'ANGELO'.split("").map((char,index)=>{
            const style = {"animation-delay": (0.1 + (6-index) / 10) + "s",color:'#EDA52F'};
            return <span
                aria-hidden="true"
                key={index}
                style={style}>
                {char}
            </span>;
        })
    }</span>

    

    return(
        <div className="aboutContainer">
            <div className="aboutTitle" style={{position:'relative',cursor:'pointer'}}>
                {!toggled && <h1 style={{fontFamily:'arial',fontSize:60,position:'absolute',color:'black',userSelect:'none',opacity:0.2}}>
                    {mangeloSpan}
                </h1>}
                {toggled && toggledRight && <h1 style={{fontFamily:'arial',fontSize:60,position:'absolute',color:'black',userSelect:'none',opacity:0.8}}>
                    {mandySpan}
                </h1>}
                {toggled && toggledLeft &&<h1 style={{fontFamily:'arial',fontSize:60,position:'absolute',color:'black',userSelect:'none',opacity:0.8}}>
                    {angeloSpan}    
                </h1>}
                <motion.div
                    style={{position:'absolute'}} 
                    animate={{x:e(toggledLeft,toggledRight)}} 
                    transition={{duration:0.7}}
                    onClick={()=>{
                        if (!toggled){
                            setToggled(true)
                        }
                        if (!toggledLeft && !toggledRight){
                            setToggledRight(true)
                        }else if (!toggledLeft && toggledRight){
                            setToggledLeft(true)
                            setToggledRight(false)
                        }else if (toggledLeft && !toggledRight){
                            setToggledLeft(false)
                            setToggledRight(true)
                        }
                    }}
                    dragElastic={false}
                    >
                    <motion.div animate={{scale:[0.8,1,0.8]}} transition={{repeat:Infinity,duration:2}} style={knowbStyle} />
                </motion.div>
            </div>
            <div className="contentContainer">
                <div className="descCol" style={{visibility:toggledRight?'visible':'hidden'}}>
                    <h1>Amanda Blanco</h1>
                    <p>a.k.a Mandy</p>
                    <p>
                    Graphic designer, crossfitter and curious person with a thirst for creativity
                    </p>
                    <p>
                        Costarican moved to spain, enjoyed the sun and becam a freelance and have been having the time of my life
                    </p>
                </div>
                <div className="photoCol" style={{backgroundImage:`url(${im})`,backgroundPosition:'center',backgroundRepeat:'no-repeat',backgroundSize:'cover'}}>
                </div>
                <div className="descCol" style={{visibility:toggledLeft?'visible':'hidden'}}>
                    <h1>Angelo Rosace</h1>
                    <p>a.k.a Accilo</p>
                    <p>
                        Software engineer with an unquenchable thirst for learning new skills. Lot's of creativity and powerfull will to explore. 
                    </p>
                    <p>
                        Italian, moved to Barcelona for a Masters stayed for a PhD
                    </p>
                </div>
            </div>
        </div>
    )
}

export default About

